.navbar{
    background-color: var(--yellow-theme-nav-background-color);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    z-index: 1;

    &__container{
        display: flex;
        align-items: center;
        width: 100%;
        height: inherit;
        padding: 0px 20px;
        flex:1;


    &__logo{
        color:var(--yellow-theme-nav-text-color);
    }

    &__menu{
        display: flex;
        list-style: none;
        text-align: center;


        &__item{
            line-height: 40px;
            margin-right: 1.5rem;
            font-size: 1.6rem;

            &::after{
                content: '';
                display: block;
                height:3px;
                width: 0;
                background: transparent;
                transition: all 0.5s ease;


            }
            &:hover::after{
                width: 100%;
                background: var(--yellow-theme-nav-text-color);
            }
            
            &__link{
                color: var(--yellow-theme-nav-text-color);
                text-decoration: none;
                padding: 0.5rem;

            }
            
        }
    }
    }


}

.nav-icon{
    display: none;
}

@media only screen and (max-width:768px){
    
    .navbar__container__menu__item__link{
        color: var(--yellow-theme-main-color)
    }
    .navbar__container__menu{
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        left:-110%;
        position: absolute;
        top: 80px;
        opacity: 1;
        transition: all 0.5 ease;


        &.active{
            background: #000;
            left:0px;
            opacity: 1;

        }
        &__item{
            border-bottom: 1px solid #ffdd40;
            margin:0px;
            padding: 20px 0px;
        }  
    }
    .nav-icon{
        display: block;
        cursor: pointer;
        color: var(--yellow-theme-main-color);
        padding-right: 20px;
    } 

}
